import { createApp } from 'vue';
import { createI18n } from 'vue-i18n';
import App from './App.vue';

// Import translation files for each page
import deHome from './locales/de/homepage.json';

import enHome from './locales/en/homepage.json';
// import enAbout from './locales/en/about.json';
// import enContact from './locales/en/contact.json';

import trHome from './locales/tr/homepage.json';
// import trAbout from './locales/tr/about.json';
// import trContact from './locales/tr/contact.json';

// Combine translations by language
const messages = {
  de: {
    home: deHome,
  },
  en: {
    home: enHome,
    // about: enAbout,
    // contact: enContact
  },
  tr: {
    home: trHome,
    // about: trAbout,
    // contact: trContact
  }
};

// Create the i18n instance
const i18n = createI18n({
  locale: 'en',
  fallbackLocale: 'en',
  messages
});

// Create and mount the Vue app
const app = createApp(App);
app.use(i18n);
app.mount('#app');
