  mounted() {
    this.handleResize();
    
    // Diğer gerekli event listener'ları ekleyin
    window.addEventListener('resize', this.handleResize);
    window.addEventListener("scroll", this.handleScroll);
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.handleResize);
    window.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    handleResize() {
      const root = document.documentElement;
      const targetMaxWidth = (window.innerWidth > 1400) ? 1400 : window.innerWidth;
      root.style.setProperty("--max-width", targetMaxWidth + 'px');
    },
    handleScroll() {
      const scrollPosition = window.scrollY || window.pageYOffset;
      if (scrollPosition > 1100 && scrollPosition < 2000) {
        // Root color changes based on scroll position, can be customized
      }
    }
  }
};
