<template>
    <div class="energy-container" :style="{ backgroundImage: `url(${hoveredImage})` }">
        <div v-for="(data, index) in companyObjects" :key="index" class="energy-object"
            @mouseover="setHoveredData(data)" @mouseleave="clearHoveredData">
            <div class="energy-object-items">
                <p class="title">{{ data.title }}</p>
                <div v-if="hoveredTitle !== data.title" class="details">
                    <div class="icon-holder icon-holder-default">
                        <div class="icon"></div>
                    </div>
                </div>
                <div v-if="hoveredTitle === data.title" class="details">
                    <p class="detail-txt">{{ data.detail }}</p>
                    <a :href='data.link' class="go-btn">
                        <p>Daha Fazla</p>
                        <div class="icon-holder icon-holder-hover">
                            <div class="icon icon-hover"></div>
                        </div>
                    </a>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'EnergyContainer',
    data() {
        return {
            hoveredTitle: '',
            hoveredImage: '',
            companyObjects: [
                {
                    title: "Maviliman Enerji",
                    detail: "Bu istasyon ile ilgili birkaç küçük bilgi bu alanda sergilenecektir.",
                    link: "/maviliman-enerji",
                    image: require('@/assets/images/energy-container/maviliman-enerji.jpg')
                },
                {
                    title: "Doğusan Petrol",
                    detail: "Bu istasyon ile ilgili birkaç küçük bilgi bu alanda sergilenecektir.",
                    link: "/dogusan-enerji",
                    image: require('@/assets/images/energy-container/dogusan-petrol.jpg')
                },
                {
                    title: "Global Petrol",
                    detail: "Bu istasyon ile ilgili birkaç küçük bilgi bu alanda sergilenecektir.",
                    link: "/global-petrol",
                    image: require('@/assets/images/energy-container/global-petrol.jpg')
                },
                {
                    title: "Saltan Petrol",
                    detail: "Bu istasyon ile ilgili birkaç küçük bilgi bu alanda sergilenecektir.",
                    link: "/saltan-petrol",
                    image: require('@/assets/images/energy-container/saltan-petrol.jpg')
                }
            ]
        };
    },
    methods: {
        setHoveredData(data) {
            this.hoveredTitle = data.title;
            this.hoveredImage = data.image;
        },
        clearHoveredData() {
            this.hoveredTitle = '';
            this.hoveredImage = require('@/assets/images/energy-container/maviliman-enerji.jpg'); // Varsayılan arka plan resmi
        }
    },
    mounted() {
        this.hoveredImage = require('@/assets/images/energy-container/maviliman-enerji.jpg'); // Başlangıç arka plan resmi
    }
}
</script>

